

.Main {
    display: grid;
    grid-template-columns: repeat(3,1fr);
}
.img{
    height: 70px;
    width: 70px;
    border-radius: 35px;
    margin-left: 15px;
    margin-top: 15px;
}
.button{
    margin-left: 24px;
    margin-top: 5px;
    font-size: 15px;
    color : #f1fff1;
    background-color: #418cff;
    border: none;
    border-bottom: 4px solid #ff8377;
    cursor: pointer;
}
.button:hover {
    transition: all 0.5s;
    background: #fff;
    color: #418cff;
}
.Names{
    margin-left: 30px;
}
.Posts{

    margin-top: 20px;
}
.Country{
    margin-top: 25px;
}
.City{
    margin-top: 10px;
}
