.header{
    grid-area: h;
    background-color: lightgrey;
}
.item{
    text-align: right;
    font-family: "Leelawadee";
}


.log{
    padding: 1px 11px;
    margin: 5px 0 20px 0;
    font-size: 15px;
    color : #e9ffbf;
    background-color: #24364a;
    border: none;
    border-bottom: 4px solid #7ab2ff;
    font-family: "HYPER DRIVE";

    cursor: pointer;
}
.log:hover {
    transition: all 0.5s;
    background: #fff;
    color: #f34a3a;
}

.loader{
    width: 150px;
    height: 150px;
    padding: 300px 350px;
    border-radius: 60%;


}